<template>
    <div class="dish">
        <b-container>
            <b-row>
                <b-col cols="3">
                    <img :src="imgurl" alt="" height="100" width="100">
                </b-col>
                <b-col cols="9">
                    <div class="attribute-container">
                        <ul class="ks-cboxtags">
                            <li v-for="(choice, index) in dish.choices" :key="index">
                                <input type="radio" :id="choice" :name="dish.name" :value="choice" :checked="index===dish.selected_choice" v-on:click="changeChoice(index)"><label :for="choice">{{choice}}</label>
                            </li>
                        </ul>
                    </div>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    export default {
        name: "Dish",
        props:["dish"],
        computed: {
            imgurl: function () {
                return require("@/assets/images/"+this.dish.img);
            }
        },
        methods: {
            changeChoice: function (index) {
                this.dish.selected_choice = index;
                this.$store.dispatch('updateCurrentMenuPrice');
            }
        }
    }
</script>

<style scoped>
    .dish {
        margin-bottom: 10px;
    }
    .attribute-container {
        max-width: 640px;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-size: 10px;
    }

    ul.ks-cboxtags {
        list-style: none;
        padding: 10px;
    }
    ul.ks-cboxtags li{
        display: inline;
    }
    ul.ks-cboxtags li label{
        display: inline-block;
        background-color: rgba(255, 255, 255, .9);
        border: 2px solid rgba(139, 139, 139, .3);
        color: #adadad;
        border-radius: 25px;
        white-space: nowrap;
        margin: 3px 0px;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
        transition: all .2s;
    }

    ul.ks-cboxtags li label {
        padding: 8px 12px;
        cursor: pointer;
    }

    ul.ks-cboxtags li label::before {
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        font-size: 12px;
        padding: 2px 6px 2px 2px;
        content: "\f067";
        transition: transform .3s ease-in-out;
    }

    ul.ks-cboxtags li input[type="radio"]:checked + label::before {
        content: "\f00c";
        transform: rotate(-360deg);
        transition: transform .3s ease-in-out;
    }

    ul.ks-cboxtags li input[type="radio"]:checked + label {
        border: 2px solid #1bdbf8;
        background-color: #12bbd4;
        color: #fff;
        transition: all .2s;
    }

    ul.ks-cboxtags li input[type="radio"] {
        display: absolute;
    }
    ul.ks-cboxtags li input[type="radio"] {
        position: absolute;
        opacity: 0;
    }
    ul.ks-cboxtags li input[type="radio"]:focus + label {
        border: 2px solid #e9a1ff;
    }

</style>
